import firebase from "gatsby-plugin-firebase"
import React, { useContext, useState, useEffect } from "react"
import { CATEGORIES, DIGITS, LETTERS, DIALOG_CLOSED_REASON } from "../constants"
import { useTranslate } from "../contexts/language"
import { NotifyContext } from "../contexts/notify"
import { UserContext } from "../contexts/user"
import { getRandomEmoji } from "../utils/getRandomEmoji"
import { PrimaryButton } from "./button"
import { Card } from "./card"
import { Dialog } from "./dialog"
import { Input } from "./input"
import { Similink, Title, Error } from "./primitives"
import { Text } from "./text"
import { View } from "./view"
import { recomputePathData } from "../utils/recomputePathData"

const ReadTangramDialog = ({ tangram, deferred }) => {
  const t = useTranslate()

  return (
    <Dialog
      onClose={() => deferred.reject(DIALOG_CLOSED_REASON)}
      css={{ gap: 3, minWidth: "268px" }}
    >
      <View css={{ gap: 3, overflow: "auto", flex: "1", alignItems: "center" }}>
        <Card selected tangram={tangram}></Card>
        <Text>
          {t("Earned {claps} 👏", {
            claps: tangram.claps || 0,
          })}
        </Text>
      </View>
    </Dialog>
  )
}

const SaveTangramDialog = ({ tangram, deferred }) => {
  const [tangramCopy, setTangramCopy] = useState({ ...tangram })
  const [errors, setErrors] = useState({})
  const [invalid, setInvalid] = useState(false)

  useEffect(() => {
    setInvalid(Object.values(errors).some((error) => error))
  }, [errors])

  const [emoji, setEmoji] = useState(
    tangram.category !== "digits" && tangram.category !== "letters"
      ? tangram.emoji
      : ""
  )
  const [letterIndex, setLetterIndex] = useState(
    tangram.category === "letters" ? LETTERS.indexOf(tangram.emoji) : 0
  )
  const [digitIndex, setDigitIndex] = useState(
    tangram.category === "digits" ? DIGITS.indexOf(tangram.emoji) : 0
  )
  const [category, setCategory] = useState(tangram.category || CATEGORIES[0])
  const [path, setPath] = useState(tangram.path)
  const { currentUser } = useContext(UserContext)

  const isCreation = currentUser && !tangram.id

  const t = useTranslate()
  const notify = useContext(NotifyContext)

  useEffect(() => {
    try {
      const pathData = recomputePathData(path)

      setTangramCopy((prevTangramCopy) => ({
        ...prevTangramCopy,
        ...pathData,
      }))

      setErrors((prevErrors) => ({
        ...prevErrors,
        path: undefined,
      }))
    } catch (error) {
      setErrors((prevErrors) => ({
        ...prevErrors,
        path: true,
      }))
    }
  }, [path])

  useEffect(() => {
    const computedEmoji =
      category === "letters"
        ? LETTERS[letterIndex]
        : category === "digits"
        ? DIGITS[digitIndex]
        : emoji
        ? emoji
        : getRandomEmoji()

    setTangramCopy((prevTangramCopy) => ({
      ...prevTangramCopy,
      emoji: computedEmoji,
    }))
  }, [category, emoji, letterIndex, digitIndex])

  useEffect(() => {
    setTangramCopy((prevTangramCopy) => ({
      ...prevTangramCopy,
      category,
    }))
  }, [category])

  const handleDelete = async () => {
    if (window.confirm(t("Are you sure you want to delete this tangram ?"))) {
      await firebase.firestore().collection("tangrams").doc(tangram.id).delete()
      notify(t("Tangram sucessfuly deleted"))
      deferred.resolve()
    }
  }

  const handleSubmit = async (event) => {
    event.preventDefault()

    if (invalid) {
      return
    }

    if (isCreation) {
      await firebase
        .firestore()
        .collection("tangrams")
        .add({
          ...tangramCopy,
          uid: currentUser.uid,
          approved: false,
        })
      notify(t("Tangram submitted for review"))
    } else {
      await firebase
        .firestore()
        .collection("tangrams")
        .doc(tangram.id)
        .update(tangramCopy)
      notify(t("Tangram successfuly modified"))
    }

    deferred.resolve()
  }

  return (
    <Dialog
      title={
        <Title>
          {isCreation ? t("Submit your tangram") : t("Edit your tangram")}
        </Title>
      }
      onClose={() => deferred.reject(DIALOG_CLOSED_REASON)}
      as="form"
      onSubmit={handleSubmit}
      css={{ gap: 3, minWidth: "268px" }}
    >
      <View css={{ gap: 3, overflow: "auto", flex: "1" }}>
        <Card
          selected
          tangram={{ ...tangramCopy }}
          css={{ alignSelf: "center" }}
        ></Card>

        {tangram.approved && (
          <Text css={{ alignSelf: "center" }}>
            {t("Earned {claps} 👏", {
              claps: tangram.claps || 0,
            })}
          </Text>
        )}

        {currentUser && currentUser.isAdmin && (
          <>
            <View css={{ gap: 2 }}>
              <label>{t("Path")}</label>
              <Input
                as="textarea"
                name="path"
                value={path}
                onChange={(e) => {
                  setPath(e.target.value)
                }}
              ></Input>
              {errors.path && <Error>{t("Invalid path")}</Error>}
            </View>
          </>
        )}

        <View css={{ gap: 2 }}>
          <label>{t("Category")}</label>
          <Input
            as="select"
            name="category"
            value={category}
            onChange={(e) => {
              setCategory(e.target.value)
            }}
          >
            {CATEGORIES.map((category) => (
              <option key={category} value={category}>
                {t(category)}
              </option>
            ))}
          </Input>
        </View>

        {category === "digits" ? (
          <View css={{ gap: 2 }}>
            <label>{t("Victory emoji")}</label>
            <Input
              as="select"
              name="digitIndex"
              value={digitIndex}
              onChange={(e) => {
                setDigitIndex(e.target.value)
              }}
            >
              {DIGITS.map((digitEmoji, index) => (
                <option key={index} value={index}>
                  {digitEmoji}
                </option>
              ))}
            </Input>
          </View>
        ) : category === "letters" ? (
          <View css={{ gap: 2 }}>
            <label>{t("Victory emoji")}</label>
            <Input
              as="select"
              name="letterIndex"
              value={letterIndex}
              onChange={(e) => {
                setLetterIndex(e.target.value)
              }}
            >
              {LETTERS.map((letterEmoji, index) => (
                <option key={index} value={index}>
                  {letterEmoji}
                </option>
              ))}
            </Input>
          </View>
        ) : (
          <View css={{ gap: 2 }}>
            <label>{t("Victory emoji")}</label>
            <Input
              name="emoji"
              value={emoji}
              onChange={(e) => {
                setEmoji(e.target.value)
              }}
            />
            <Text as="small">{t("Leave empty for random emoji")}</Text>
          </View>
        )}
      </View>
      <PrimaryButton type="submit">
        {isCreation ? t("Submit !") : t("Edit !")}
      </PrimaryButton>
      {!isCreation && (
        <View
          css={{
            alignItems: "center",
          }}
        >
          <Similink onClick={handleDelete}>{t("Or delete tangram")}</Similink>
        </View>
      )}
    </Dialog>
  )
}

export const TangramDialog = ({ tangram, deferred }) => {
  const { currentUser } = useContext(UserContext)
  const isEdit =
    currentUser &&
    (currentUser.isAdmin || !tangram.id || tangram.uid === currentUser.uid)

  return isEdit ? (
    <SaveTangramDialog tangram={tangram} deferred={deferred} />
  ) : (
    <ReadTangramDialog tangram={tangram} deferred={deferred} />
  )
}
